// scss-docs-end theme-color-variables

// scss-docs-start theme-colors-map
// stylelint-disable-next-line scss/dollar-variable-default
$theme-colors:(
  
    "primary":    $primary,
    "secondary":  $secondary,
    "success":    $success,
    "info":       $info,
    "warning":    $warning,
    "indigo":     $indigo,
    "danger":     $danger,
    "brown":      $brown,
    "teal":       $teal,
    "lime":       $lime,
    "orange":     $orange,
    "blue-grey":  $blue-grey,
    "purple":     $purple,
    "pink":       $pink,
    "light":      $light,
    "dark":       $dark
  
);

// scss-docs-end theme-colors-map

// scss-docs-start theme-colors-rgb
