// $utilities-bg-colors: map-loop($utilities-bg, rgba-css-var, "$key", "bg");
// scss-docs-end utilities-bg-colors

// Links
//
// Style anchor elements.

$link-color:                              $primary;
$link-decoration:                         none;
$link-shade-percentage:                   20%;
$link-hover-color:                        shift-color($link-color, $link-shade-percentage);
$link-hover-decoration:                   underline;
// Darken percentage for links with `.text-*` class (e.g. `.text-success`)
$stretched-link-pseudo-element:           after;
$stretched-link-z-index:                  1;

// Paragraphs
//
// Style p element.

$paragraph-margin-bottom:   1rem;


// Grid breakpoints
//
// Define the minimum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.

// scss-docs-start grid-breakpoints
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
);
// scss-docs-end grid-breakpoints

@include _assert-ascending($grid-breakpoints, "$grid-breakpoints");
@include _assert-starts-at-zero($grid-breakpoints, "$grid-breakpoints");


// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.

// scss-docs-start container-max-widths
$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1140px,
  xxl: 1320px
);
// scss-docs-end container-max-widths

@include _assert-ascending($container-max-widths, "$container-max-widths");

// Grid columns
//
// Set the number of columns and specify the width of the gutters.

$grid-columns:                  12;
$grid-gutter-width:             1.5rem;
$grid-row-columns:              6;

// $gutters:                       $spacers;

// Container padding

$container-padding-x:           $grid-gutter-width * 1;
// Components
//
// Define common padding and border radius sizes and more.

// scss-docs-start border-variables
$border-width:                  1px;
$border-widths: (
  1: 1px,
  2: 2px,
  3: 3px,
  4: 4px,
  5: 5px
);

$border-style:                  solid;
$border-color:                  $gray-200;
$border-color-translucent:      rgba($black, .175);
// scss-docs-end border-variables

// scss-docs-start border-radius-variables
$border-radius-2xl:             2rem;
$border-radius:                 .25rem;
$border-radius-lg:              .4rem;
$border-radius-xl:              1rem;
$border-radius-sm:              .2rem;
$border-radius-pill:            50rem;
// scss-docs-end border-radius-variables

// scss-docs-start box-shadow-variables
$box-shadow-sm:                 0 .125rem .25rem rgba($black, .075);
$box-shadow:                    0px 0px 13px 0px rgba(236, 236, 241, 0.44);
$box-shadow-lg:                 0px 0px 13px 0px rgba(236, 236, 241, 0.44);
$box-shadow-inset:              inset 0 1px 2px rgba($black, .075);
// scss-docs-end box-shadow-variables

$component-active-color:        $white;
$component-active-bg:           $primary;

// scss-docs-start caret-variables
$caret-width:                   .3em;
$caret-vertical-align:          $caret-width * .85;
$caret-spacing:                 $caret-width * .85;
// scss-docs-end caret-variables

$transition-base:               all .2s ease-in-out;
$transition-fade:               opacity .15s linear;
// scss-docs-start collapse-transition
$transition-collapse:           height .35s ease;
$transition-collapse-width:     width .35s ease;
// scss-docs-end collapse-transition

// stylelint-disable function-disallowed-list
// scss-docs-start aspect-ratios
$aspect-ratios: (
  "1x1":                        100%,
  "4x3":                        calc(3 / 4 * 100%),
  "16x9":                       calc(9 / 16 * 100%),
  "21x9":                       calc(9 / 21 * 100%)
);
// scss-docs-end aspect-ratios
// stylelint-enable function-disallowed-list

// Typography
//
// Font, line-height, and color for body text, headings, and more.

// scss-docs-start font-variables
// stylelint-disable value-keyword-case
$font-family-sans-serif:      'Roboto', sans-serif;
$font-family-monospace:       SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;


// stylelint-enable value-keyword-case
$font-family-base:            var(--#{$prefix}font-sans-serif);
$font-family-code:            var(--#{$prefix}font-monospace);

// $font-size-root effects the value of `rem`, which is used for as well font sizes, paddings and margins
// $font-size-base effects the font size of the body text

$font-size-root:              null;
$font-size-base:              0.875rem;
$font-size-sm:                $font-size-base * .875;
$font-size-lg:                $font-size-base * 1.25;


$font-weight-lighter:         lighter;
$font-weight-light:           300;
$font-weight-normal:          400;
